.shape {
    height: 100px;
    width: 100px;
    display: inline-block;
    /* margin-bottom: -1%; */
    background-color: #eee;
    border: 1px solid black;
}

.shape.blocker{
    background-color: black;
}

@media(max-width: 800px){
    .shape {
        height: 50px;
        width: 50px;
        display: inline-block;
        background-color: #eee;
        border: 1px solid black;
    }
}

